import Login from './views/Login.vue'
import NotFound from './views/404.vue'
import Home from './views/Home.vue'
import DashBoard from './components/dashboard/DashBoard.vue'
import Table from './views/nav1/Table.vue'
import Form from './views/nav1/Form.vue'
import user from './views/nav1/user.vue'
import Page4 from './views/nav2/Page4.vue'
import Page5 from './views/nav2/Page5.vue'
import Page6 from './views/nav3/Page6.vue'
import Groups from './views/groups/Groups.vue'
import echarts from './views/charts/echarts.vue'

let routes = [
    {
        path: '/login',
        component: Login,
        name: '',
        hidden: true
    },
    {
        path: '/404',
        component: NotFound,
        name: '',
        hidden: true
    },
    {
        path: '/',
        component: Home,
        name: '',
        iconCls: 'fa fa-home',
        leaf: true,//
        children: [
            { path: '/dashboard', component: DashBoard, name: ''},
        ]
    },
    {
        path: '/',
        component: Home,
        name: '',
        iconCls: 'el-icon-menu',//class
        children: [
            { path: '/table', component: Table, name: 'zookeeper' },
            { path: '/form', component: Form, name: 'agent' },
            { path: '/user', component: user, name: 'feedback' },
        ]
    },
    {
        path: '/',
        component: Home,
        name: '',
        iconCls: 'el-icon-menu',
        children: [
            { path: '/page4', component: Page4, name: '4' },
            { path: '/page5', component: Page5, name: '5' }
        ]
    },
    {
        path: '/',
        component: Home,
        name: '',
        iconCls: 'el-icon-menu',
        children: [
            { path: '/groups', component: Groups, name: '' },
            { path: '/groups', component: Groups, name: '' }
        ]
    },
    {
        path: '/',
        component: Home,
        name: 'zyagent',
        iconCls: 'el-icon-menu',
        children: [
            { path: '/echarts', component: echarts, name: 'echarts' }
        ]
    },
    {
        path: '*',
        hidden: true,
        redirect: { path: '/404' }
    }
];

export default routes;



